import Util from 'common/util.js';
export default{
	data(){
		return {
			eid:null,
			flag:false
			};
	},
	onLoad(option){
		this.eid=parseInt(option.eid);
		
	},
	methods:{
		startExam(){
			uni.navigateTo({
				url:'/pages/exam/start?eid='+this.eid
			})
		},
		d()
		{
			var that=this;
			
